
import { useState } from 'react';
import { Sparkles, Brain, Zap, ShieldCheck, ArrowRight, CheckCircle2, X } from 'lucide-react';
import '../styles/onboarding.css';
import { useLanguage } from '../i18n/LanguageContext';
import React from 'react';

interface OnboardingModalProps {
    onComplete: () => void;
}

export function OnboardingModal({ onComplete }: OnboardingModalProps) {
    const { t } = useLanguage();
    const [step, setStep] = useState(1);
    const totalSteps = 3;

    const nextStep = () => {
        if (step < totalSteps) {
            setStep(step + 1);
        } else {
            onComplete();
        }
    };

    const handleBack = () => {
        if (step > 1) {
            setStep(step - 1);
        }
    };

    return (
        <div className="onboarding-overlay">
            <div className="onboarding-modal">
                <button className="onboarding-skip" onClick={onComplete} aria-label="Skip onboarding">
                    <X size={20} />
                </button>

                <div
                    className="onboarding-progress"
                    style={{ width: `${(step / totalSteps) * 100}%` }}
                ></div>

                <div className="onboarding-content">
                    {step === 1 && (
                        <div className="onboarding-step">
                            <div className="onboarding-icon-badge">
                                <Sparkles size={32} />
                            </div>
                            <h2 className="onboarding-title">{t('onboarding.welcome.title')}</h2>
                            <p className="onboarding-desc" dangerouslySetInnerHTML={{ __html: t('onboarding.welcome.desc') }} />
                        </div>
                    )}

                    {step === 2 && (
                        <div className="onboarding-step">
                            <div className="onboarding-icon-badge">
                                <Zap size={32} />
                            </div>
                            <h2 className="onboarding-title">{t('onboarding.hybrid.title')}</h2>
                            <p className="onboarding-desc">{t('onboarding.hybrid.desc')}</p>

                            <div className="onboarding-diagram">
                                <div className="diagram-node">
                                    <Brain size={48} />
                                </div>
                                <div className="diagram-line"></div>
                                <div className="diagram-node">
                                    <Zap size={48} />
                                </div>
                            </div>

                            <div className="onboarding-grid">
                                <div className="onboarding-feature-card">
                                    <h4>{t('onboarding.hybrid.brain')}</h4>
                                    <p>{t('onboarding.hybrid.brainDesc')}</p>
                                </div>
                                <div className="onboarding-feature-card">
                                    <h4>{t('onboarding.hybrid.muscle')}</h4>
                                    <p>{t('onboarding.hybrid.muscleDesc')}</p>
                                </div>
                            </div>
                        </div>
                    )}

                    {step === 3 && (
                        <div className="onboarding-step">
                            <div className="onboarding-icon-badge">
                                <ShieldCheck size={32} />
                            </div>
                            <h2 className="onboarding-title">{t('onboarding.protocol.title')}</h2>
                            <p className="onboarding-desc">{t('onboarding.protocol.desc')}</p>

                            <ul className="onboarding-rules">
                                <li className="onboarding-rule-item">
                                    <CheckCircle2 className="onboarding-rule-icon" size={20} />
                                    <div className="onboarding-rule-text">
                                        <h5>{t('onboarding.protocol.allocation')}</h5>
                                        <p dangerouslySetInnerHTML={{ __html: t('onboarding.protocol.allocationDesc') }} />
                                    </div>
                                </li>
                                <li className="onboarding-rule-item">
                                    <CheckCircle2 className="onboarding-rule-icon" size={20} />
                                    <div className="onboarding-rule-text">
                                        <h5>{t('onboarding.protocol.agreement')}</h5>
                                        <p dangerouslySetInnerHTML={{ __html: t('onboarding.protocol.agreementDesc') }} />
                                    </div>
                                </li>
                            </ul>
                        </div>
                    )}

                    <div className="onboarding-actions">
                        {step > 1 && (
                            <button className="onboarding-btn secondary" onClick={handleBack}>
                                {t('common.back')}
                            </button>
                        )}
                        <button className="onboarding-btn primary" onClick={nextStep}>
                            {step === totalSteps ? t('onboarding.start') : t('common.next')}
                            <ArrowRight size={18} />
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}
