import { useState, useEffect } from 'react';
import { UniversalLink as Link } from "../../database/UniversalLink";
import { useUniversalSearchParams as useSearchParams } from "../../database/useUniversalSearchParams";

import { Sparkles, CreditCard, Plus, User, Zap, LogOut } from 'lucide-react';
import '../../styles/dashboard.css';
import { useLanguage } from '../../i18n/LanguageContext';
import * as api from '../../services/api';
import type { Gem } from '../../types/schema';
import { OnboardingModal } from '../../components/OnboardingModal';
import React from 'react';

// Helper for Generated Image (locally used type for UI state vs API type)
interface UIGeneratedImage {
    url: string;
    caption: string;
}

export function AppPage() {
    const { t, language } = useLanguage();
    const [searchParams, setSearchParams] = useSearchParams();

    // API State
    const [balance, setBalance] = useState(0);
    const [activeGemId, setActiveGemId] = useState('gem-idea');
    const [gems, setGems] = useState<Gem[]>([]);

    // UI State
    const [gemOutputText, setGemOutputText] = useState('');
    const [userMessage, setUserMessage] = useState('');
    const [isThinking, setIsThinking] = useState(false);
    const [musclePrompt, setMusclePrompt] = useState('');
    const [isRendering, setIsRendering] = useState(false);
    const [generatedImage, setGeneratedImage] = useState<UIGeneratedImage | null>(null);
    const [showLimitModal, setShowLimitModal] = useState(false);
    const [showGemTooltip, setShowGemTooltip] = useState(false);
    const [showOnboarding, setShowOnboarding] = useState(false);

    // Initial Data Fetch
    useEffect(() => {
        const fetchData = async () => {
            try {
                const [userCredits, apiGems] = await Promise.all([
                    api.getUserCredits(),
                    api.getGems()
                ]);
                setBalance(userCredits);
                setGems(apiGems);
            } catch (err) {
                console.error("Failed to load app data", err);
            }
        };
        fetchData();
    }, []);

    // Onboarding Check
    useEffect(() => {
        const hasOnboarded = localStorage.getItem('has_onboarded');
        const onboardingParam = searchParams.get('onboarding');

        if (onboardingParam === 'true' || !hasOnboarded) {
            setShowOnboarding(true);
        }
    }, [searchParams]);

    const handleOnboardingComplete = () => {
        setShowOnboarding(false);
        localStorage.setItem('has_onboarded', 'true');
        // Clear param without reload
        setSearchParams({}, { replace: true });
    };

    // Tooltip persistence
    useEffect(() => {
        if (showOnboarding) return;

        const hasSeenGemTooltip = localStorage.getItem('hasSeenGemTooltip');
        if (!hasSeenGemTooltip) {
            const timer = setTimeout(() => {
                setShowGemTooltip(true);
                localStorage.setItem('hasSeenGemTooltip', 'true');
            }, 1000);
            return () => clearTimeout(timer);
        }
    }, [gems, showOnboarding]);

    const iconMap: Record<string, React.ReactNode> = {
        'gem-idea': <Sparkles size={20} />,
        'gem-scriptwriter': <User size={20} />,
        'gem-visualizer': <Plus size={20} />
    };

    const currentGem = gems.find(g => g.id === activeGemId);

    // Helper to get localized text
    const getGemTitle = (gem?: Gem) => language === 'vn' ? gem?.title_vi : gem?.title_en;

    const handleGemSubmit = async () => {
        if (!userMessage.trim() || isThinking || !currentGem) return;
        setIsThinking(true);

        // Mock gemChat call (still simulated locally as per task scope for "Gems" strictly API for lists?)
        // The prompt says "Gems: getGems(), getFreeGems()". It doesn't explicitly mention a 'chatWithGem' mock function.
        // It says "UI must depend on these functions". 
        // But for "ImageGeneration" it has `createImageGeneration`.
        // I will keep the chat logic simulated here or move it to a helper if strictness requires, 
        // but the prompt didn't ask for a `chatWithGem` function in the MOCK FUNCTIONS list.
        // So I will keep the simulation here to handle the "thinking" phase which is "free".

        setTimeout(() => {
            const gemName = getGemTitle(currentGem);
            const response = `[${gemName} Output]\nDetailed visual prompt strategy for "${userMessage}":\n\n"A cinematic masterwork of ${userMessage}, captured with high-speed lenses, dramatic chiaroscuro lighting, and extreme textural fidelity. Rendered in 8k for H100 GPU clusters."`;
            setGemOutputText(response);
            setMusclePrompt(response);
            setIsThinking(false);
            setUserMessage('');
        }, 1500);
    };

    const handleGenerateImage = async () => {
        if (!musclePrompt.trim() || isRendering) return;

        setIsRendering(true);

        try {
            const result = await api.createImageGeneration(musclePrompt);

            // Sync balance
            const newBalance = await api.getUserCredits();
            setBalance(newBalance);

            setGeneratedImage({
                url: result.image_url,
                caption: `Generated using ${getGemTitle(currentGem)} + A.ID Gen`
            });

            // Scroll to output
            setTimeout(() => {
                window.scrollTo({ top: document.body.scrollHeight, behavior: 'smooth' });
            }, 100);

        } catch (error: any) {
            console.error("Generation failed", error);
            if (error.message === 'Insufficient credits') {
                setShowLimitModal(true);
            }
        } finally {
            setIsRendering(false);
        }
    };

    return (
        <div className="app-container">

            {/* 6️⃣ OUT-OF-CREDITS MODAL */}
            {showOnboarding && <OnboardingModal onComplete={handleOnboardingComplete} />}
            {showLimitModal && (
                <div className="limit-overlay">
                    <div className="limit-card">
                        <div className="limit-icon"><Zap size={32} /></div>
                        <h2 className="limit-title">{t('app.limitModal.title')}</h2>
                        <p className="limit-desc" dangerouslySetInnerHTML={{ __html: t('app.limitModal.desc') }} />
                        <div style={{ display: 'flex', flexDirection: 'column', gap: '12px', width: '100%' }}>
                            <Link to="/buy" className="generate-btn" style={{ textDecoration: 'none' }}>
                                <CreditCard size={18} />
                                {t('app.limitModal.getMore')}
                            </Link>
                            <button onClick={() => setShowLimitModal(false)} className="checkout-cta-btn">
                                {t('app.limitModal.continue')}
                            </button>
                        </div>
                    </div>
                </div>
            )}

            {/* 1️⃣ TOP BAR */}
            <header className="app-top-bar">
                <div className="top-bar-inner">
                    <Link to="/app" className="top-bar-logo">
                        <div className="logo-dot"></div>
                        <span>A.ID</span>
                    </Link>

                    <div className="top-bar-actions">
                        <div className="balance-container">
                            <CreditCard size={16} color="#60a5fa" />
                            <div className="balance-info">
                                <span className="balance-label">{t('app.balance')}</span>
                                <span className="balance-value">{balance} {t('app.imagesCount')}</span>
                            </div>
                        </div>
                        <Link to="/buy" className="buy-images-link" style={{ background: '#3b82f6', color: '#fff' }}>
                            <Plus size={16} />
                            <span>{t('app.buyMore')}</span>
                        </Link>
                        <Link to="/logout" className="logout-link">
                            <LogOut size={16} />
                            <span>{t('Logout') || 'Logout'}</span>
                        </Link>
                    </div>
                </div>
            </header>

            <div className="app-bg-glow"></div>

            <main className="app-main">
                <div className="app-content-column">

                    {/* 2️⃣ GEM SELECTOR */}
                    <div className="app-section centered">
                        <h4 className="section-label">{t('app.selectGem')}</h4>
                        <div style={{ position: 'relative' }}>
                            {showGemTooltip && (
                                <div className="gem-tooltip">
                                    {t('app.gemTooltip')}
                                    <button onClick={() => setShowGemTooltip(false)} className="ml-2 opacity-70">✕</button>
                                </div>
                            )}
                            <div className="gem-grid">
                                {gems.map(gem => (
                                    <div
                                        key={gem.id}
                                        className={`gem-card ${activeGemId === gem.id ? 'active' : ''}`}
                                        onClick={() => setActiveGemId(gem.id)}
                                    >
                                        <div className="gem-icon">{iconMap[gem.id] || <Sparkles size={20} />}</div>
                                        <span className="gem-name">{getGemTitle(gem)}</span>
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>

                    {/* 3️⃣ GEM OUTPUT PANEL */}
                    <div className="app-section">
                        <h4 className="section-label">{t('app.brainTitle')}</h4>
                        <div className="gem-output-panel">
                            <textarea
                                className="gem-textarea"
                                placeholder={t('app.askGem').replace('{name}', getGemTitle(currentGem) || '')}
                                value={userMessage}
                                onChange={(e) => setUserMessage(e.target.value)}
                            />
                            <button className="submit-gem-btn" onClick={handleGemSubmit} disabled={isThinking}>
                                {isThinking ? t('app.thinking') : `${t('app.analyze')} ${getGemTitle(currentGem) || 'GEM'}`}
                            </button>

                            <div className="gem-ai-output">
                                {gemOutputText || t('app.gemPlaceholder')}
                            </div>

                            <div className="gem-footer-note" dangerouslySetInnerHTML={{ __html: t('app.gemNote') }} />
                        </div>
                    </div>

                    {/* 4️⃣ IMAGE GENERATION PANEL */}
                    <div className="app-section">
                        <h4 className="section-label">{t('app.muscleTitle')}</h4>
                        <div className="muscle-panel">
                            <textarea
                                className="muscle-textarea"
                                value={musclePrompt}
                                onChange={(e) => setMusclePrompt(e.target.value)}
                                placeholder={t('app.musclePlaceholder')}
                            />
                            <button
                                className="generate-btn"
                                onClick={handleGenerateImage}
                                disabled={isRendering}
                                style={{ marginTop: '24px' }}
                            >
                                {isRendering ? t('app.rendering') : t('app.generate')}
                            </button>
                        </div>
                    </div>

                    {/* 5️⃣ IMAGE OUTPUT PANEL */}
                    {generatedImage && (
                        <div className="app-section centered" style={{ borderBottom: 'none' }}>
                            <h4 className="section-label">{t('app.renderResult')}</h4>
                            <div className="image-output-container">
                                <div className="generated-image-wrapper">
                                    <img src={generatedImage.url} alt="AI Result" className="generated-image" />
                                </div>
                                <div className="image-caption">
                                    {generatedImage.caption}
                                </div>
                            </div>
                        </div>
                    )}

                </div>
            </main>
        </div>
    );
}
